import apiClient from "./apiClient";
// تایپ‌ها را برای تمیزی به یک فایل جداگانه مانند services/api/types.ts منتقل می‌کنیم
// اما فعلاً می‌توانند اینجا باشند.
export interface Shop {
  id: string;
  name: string;
  lastname: string;
  shopTitle: string;
  status: "Ok" | "Pending" | "Rejected";
  registrationStatus: "Confirmed" | "Pending" | "Rejected";
  createDate: string;
  description: string; // این فیلد را برای صفحه جزئیات اضافه می‌کنیم
  logo?: string | null; 
}

export interface PaginatedResponse<T> {
  currentPage: number;
  totalPages: number;
  pageSize: number;
  totalCount: number;
  items: T[];
}


/**
 * لیستی از فروشگاه‌ها را به صورت صفحه‌بندی شده واکشی می‌کند.
 */
export const getShops = (
  pageNumber: number, 
  pageSize: number
): Promise<PaginatedResponse<Shop>> => {
  return apiClient.get('A_Shop/GetShops', {
    params: {
      PageNumber: pageNumber,
      PageSize: pageSize,
    }
  });
};

/**
 * ✨ --- کلمه کلیدی export در اینجا اضافه شد --- ✨
 * اطلاعات یک فروشگاه خاص را بر اساس شناسه (ID) واکشی می‌کند.
 * @param shopId شناسه فروشگاه مورد نظر
 * @returns یک Promise که به آبجکت Shop حل می‌شود.
 */
export const getShopById = async (shopId: string): Promise<Shop> => {
  const response: PaginatedResponse<Shop> = await apiClient.get('A_Shop/GetShops', {
    params: {
      Ids: shopId,
      PageNumber: 1,
      PageSize: 1,
    }
  });

  if (response && response.items && response.items.length > 0) {
    return response.items[0];
  }

  throw new Error(`فروشگاهی با شناسه ${shopId} یافت نشد.`);
};