"use client"; // این صفحه برای واکشی داده و مدیریت state به حالت کلاینت نیاز دارد

import React, { useState, useEffect, ReactNode } from "react";
import Link from "next/link";
import { usePathname } from "next/navigation";

// ۱. وارد کردن کامپوننت‌های لازم از design-system
import { YadakchiAdminLayout, ShopDetailsCard } from "@my-ds/design-system";

// ۲. وارد کردن سرویس API و تایپ‌های مربوطه
import { getShopById, type Shop } from "../../../services/shopsService";

// ۳. وارد کردن ابزارهای کمکی
import { translateStatus } from "../../../utils/translations";

// ۴. تعریف پراپرتی‌های ورودی صفحه
// Next.js به صورت خودکار پارامترهای داینامیک (مانند id) را به prop `params` پاس می‌دهد.
interface ShopDetailsPageProps {
  params: {
    id: string; // id از نام پوشه [id] می‌آید
  }
}

export default function ShopDetailsPage({ params }: ShopDetailsPageProps) {
  const { id } = params; // استخراج ID از پارامترها
  
  // ۵. State برای نگه داشتن داده‌ها، لودینگ و خطا
  const [shop, setShop] = useState<Shop | null>(null);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  const pathname = usePathname();
  const renderLink = (href: string, children: ReactNode) => (
    <Link href={href}>{children}</Link>
  );

  // ۶. واکشی داده‌ها در اولین رندر کامپوننت، بر اساس ID
  useEffect(() => {
    // اگر ID وجود نداشت، هیچ کاری نکن
    if (!id) {
        setIsLoading(false);
        setError("شناسه فروشگاه نامعتبر است.");
        return;
    }

    const fetchShop = async () => {
      try {
        setIsLoading(true);
        setError(null);
        const shopData = await getShopById(id);
        setShop(shopData);
      } catch (err) {
        setError(err instanceof Error ? err.message : "یک خطای ناشناخته رخ داد.");
      } finally {
        setIsLoading(false);
      }
    };

    fetchShop();
  }, [id]); // این افکت فقط زمانی اجرا می‌شود که ID تغییر کند

  // ۷. یک تابع برای رندر کردن محتوای اصلی بر اساس وضعیت
  const renderContent = () => {
    if (isLoading) {
      return <p>در حال بارگذاری اطلاعات فروشگاه...</p>;
    }
    if (error) {
      return <p style={{ color: 'red' }}>خطا: {error}</p>;
    }
    if (!shop) {
      return <p>اطلاعات فروشگاه یافت نشد.</p>;
    }

    // اگر داده‌ها با موفقیت دریافت شدند، کامپوننت را با داده‌های واقعی رندر کن
    return (
      <ShopDetailsCard
        shopLogo={shop.logo}
        shopTitle={shop.shopTitle}
        shopStatus={translateStatus(shop.registrationStatus)}
        shopStatusVariant={
          shop.registrationStatus === "Confirmed" ? "success" : "warning"
        }
        items={[
          { title: "نام فروشنده", value: `${shop.name} ${shop.lastname}` },
          { title: "توضیحات", value: shop.description || "-" },
          { title: "وب‌سایت", value: shop.website || "-" },
          { title: "تلفن", value: shop.tell || "-" },
          { title: "شماره همراه", value: shop.phoneNumber || "-" },
        ]}
      />
    );
  };

  return (
    <YadakchiAdminLayout currentPath={pathname} renderLink={renderLink}>
      {/* می‌توانید از breadcrumb هم در اینجا استفاده کنید */}
      <h1 className="text-2xl font-semibold mb-4">جزئیات فروشگاه</h1>
      
      {renderContent()}
    </YadakchiAdminLayout>
  );
}